import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import Swal from 'sweetalert2'
import store from '../store'
import router from '../router'

export const api = axios.create({
  baseURL: process.env.VUE_APP_API
})

// axios ÊîîÊà™Âô®
// axios ÂëºÂè´ÂõûÊáâÊôÇ --> ÊîîÊà™Âô® --> ÂëºÂè´ÁöÑÂú∞ÊñπÁöÑ trycatch
api.interceptors.response.use(res => {
  return res
}, error => {
  // Â¶ÇÊûú axios ÊúâÊî∂Âà∞ÂõûÂÇ≥ÁöÑÊù±Ë•ø
  if (error.response) {
    // Â¶ÇÊûúÈåØË™§ÊòØ 401ÔºåJWT ÂèØËÉΩÈÅéÊúü‰∫Ü
    if (error.response.status === 401) {
      // Â¶ÇÊûúÂéüÂßãË´ãÊ±ÇÁöÑ‰ΩçÂùÄ‰∏çÊòØÂª∂Èï∑ÁôªÂÖ•ÔºåÊâçÂª∂Èï∑ÁôªÂÖ•
      if (error.config.url !== '/users/extend') {
        // ÂéüÂßãË´ãÊ±ÇÁöÑË®≠ÂÆö
        const originalRequest = error.config
        // ÂÇ≥ÈÄÅÂª∂Èï∑ÁôªÂÖ•ÁöÑË´ãÊ±Ç
        return api.post('/users/extend', {}, {
          headers: {
            authorization: 'Bearer ' + store.state.user.token
          }
        }).then(({ data }) => {
          // Â¶ÇÊûúÂª∂Èï∑ÁôªÂÖ•Ë´ãÊ±ÇÊàêÂäüÔºåÊõ¥Êñ∞ vuex ÁöÑ token
          store.commit('user/extend', data.result.token)
          // Êõ¥Êñ∞ÂéüÂßãË´ãÊ±ÇÁöÑ token
          originalRequest.headers.authorization = 'Bearer ' + store.state.user.token
          // ÈáçÊñ∞ÂÇ≥ÈÄÅ‰∏ÄÊ¨°Ë´ãÊ±Ç‰∏¶ÂõûÂÇ≥Êñ∞Ë´ãÊ±Ç
          return axios(originalRequest)
        }).catch(error => {
          // Â¶ÇÊûúÂª∂Èï∑ÁôªÂÖ•Ë´ãÊ±ÇÂ§±ÊïóÔºåÁôªÂá∫
          store.commit('user/logout')
          router.push('/login')
          // Â∞áÂª∂Èï∑Ë´ãÊ±ÇÁöÑÈåØË™§ÂõûÂÇ≥
          return Promise.reject(error)
        })
      }
    }
  } else {
    if (error.code === 'ECONNABORTED' && error.message.includes('timeout')) {
      Swal.fire({
        icon: 'error',
        title: 'ÈåØË™§',
        text: 'Ë´ãÊ±ÇÈÄæÊôÇ'
      })
    } else {
      Swal.fire({
        icon: 'error',
        title: 'ÈåØË™§',
        text: 'Á∂≤Ë∑Ø‰∏çÁ©©ÂÆö'
      })
    }
  }
  return Promise.reject(error)
})

Vue.use(VueAxios, { axios, api })
